import React from "react"
import { graphql } from 'gatsby'
import BackButton from '../components/back-button'
import '../components/styles/custom.sass'

export default function photoTemplate({ data }) {
  const post = data.markdownRemark;
  return <div id="photo">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="photo-post" style={{ paddingTop: '25px' }}>
              <BackButton />
              <h1 className="template-h1">
                {post.frontmatter.title}
              </h1>
              <hr style={{ marginTop: '50px' }}/>
            </div>
          </div>
        </div>
      </div>
      <div className="photo-post-content" dangerouslySetInnerHTML={{ __html: post.html }} style={{ padding: `0 25px`, }}/>
    </div>
}

export const postQuery = graphql`
  query photoPostByPath($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        date
        path
        title
        author
        template
      }
    }
  }
`;

